.cor-parralax {
	position: relative;
	padding: 120px 0;
	text-align: center;
	background: $purp;
	overflow: hidden;
	@include pseudo;
	&::before {
		top: 0;
		left: 0;
		z-index: 2;
		width: 100%;
		height: 100%;
		background-color: rgba($purp-dark, .7); }
	&_top {
		font-family: $proxima;
		@include fs(40);
		font-weight: 400;
		p:not(:last-of-type) {
			margin-bottom: 30px; }
		.cor-parralax-rellax {
			@include image('cor-parll-1-2.jpg'); } }
	&_bottom {
		.cor-parralax {
			&__title {
				margin-bottom: 35px;
				font-family: $proxima;
				@include fs(36);
				font-weight: 600;
				color: $light; }
			&-list {
				margin: 0 auto 40px;
				width: 60%;
				text-align: left;
				list-style-type: decimal;
				&__li {
					margin-bottom: 20px;
					@include fs(22);
					font-weight: 400; } }
			&-rellax {
				top: 0;
				min-height: 250%;
				@include image('cor-parll-2-2.jpg'); }
			&__text {
				@include fs(33);
				font-weight: 600; } }
		.btn {
			margin-bottom: 50px;
			min-width: 340px; } }

	.container {
		position: relative;
		z-index: 2;
		color: $light; } }

.cor-parralax-rellax {
	position: absolute;
	top: -25%;
	left: 0;
	width: 100%;
	min-height: 200%;
	text-align: center;
	@include cover;
	background-position: center;
	img {
		display: inline-block;
		vertical-align: middle;
		min-width: 100%;
		min-height: 100%;
		width: auto;
		height: auto; } }

