.hero {
	position: relative;
	margin-top: 90px;
	display: flex;
	&-content {
		position: relative;
		z-index: 2;
		display: flex;
		flex-direction: column;
		justify-content: flex-end;
		width: 100%;
		min-height: calc(100vh - 90px);
		margin: auto auto 0;
		&__title {
			box-sizing: border-box;
			padding: 35px 0;
			width: 70%;
			font-family: $proxima;
			@include fs(50);
			font-weight: 700;
			color: $light; }
		&-form {
			padding: 20px 0 30px;
			width: 100%;
			background-color: rgba($purp-dark, .7);
			&__text {
				display: block;
				margin-bottom: 10px;
				// text-align: center
				font-family: $proxima;
				@include fs(30);
				font-weight: 600;
				color: $light; } } }

	&-video {
		position: absolute;
		top: 0;
		left: 0;
		z-index: 1;
		width: 100%;
		height: 100%;
		overflow: hidden;
		@include image('video-poster.jpg');
		@include cover;
		&::before {
			content: '';
			position: absolute;
			top: 0;
			left: 0;
			z-index: 1;
			width: 100%;
			height: 100%;
			background-color: rgba($purp-dark, .7); }
		&__video {
			position: absolute;
			top: 50%;
			left: 50%;
			z-index: 0;
			min-width: 105%;
			min-height: 105%;
			width: auto;
			height: auto;
			transform: translate(-50%, -50%); } } }

.hero-form {
	display: flex;
	justify-content: space-between;
	& > * {
		flex: 1;
		margin-right: 30px; }
	.btn {
		margin-right: 0; } }
