.screen {
	padding: 45px 0 75px;
	&-title {
		margin: 0 auto 60px;
		padding-bottom: 10px;
		width: auto;
		text-align: center;
		font-family: $proxima;
		@include fs(33);
		font-weight: 600;
		color: $blue-dark;
		&__content {
			display: inline;
			vertical-align: middle;
			@include pseudo;
			&::before {
				top: calc(100% + 6px);
				width: 100%;
				height: 4px;
				@include g(#3d163b, #872c4c);
				opacity: .25; } } } }
