.input {
	display: inline-block;
	vertical-align: middle;
	box-sizing: border-box;
	padding: 0 15px;
	font-family: $proxima;
	@include fs(18);
	font-weight: 600;
	color: $blue-dark;
	background-color: $light;
	border: 2px solid transparent;
	transition: .25s;
	&:hover {
		border-color: $grad-end; }
	&:focus {
		border-color: $grad-start; } }
